<template>
  <div class="col-xs-1" align="center"><br><br><br>
    <h1>Welcome to Countdown</h1>
    <h2>Register or login to get started</h2>
    <form v-on:submit.prevent="register">
      <p>1. Choose a username</p>
      <input class="narrow" v-model="username" placeholder="User Name">
      <p>2. Create an account</p>
      <input class="wide" v-model="name" placeholder="First and Last Name"><br/>
      <input class="narrow" v-model="email" placeholder="Email Address">
      <input class="narrow" type="password" v-model="password" placeholder="Password">
      <button class="alternate" type="submit">Register</button>
    </form>
    <p class="error">{{registerError}}</p>
  </div>
</template>

<script>
 export default {
   name: 'WelcomePage',
   data () {
     return {
       username: '',
       email: '',
       password: '',
       name: '',
     }
   },
   computed: {
     registerError: function() {
       return this.$store.getters.registerError;
     },
   },
   methods: {
     register: function() {
       this.$store.dispatch('register',{
         username: this.username,
         email: this.email,
         password: this.password,
         name: this.name,
       });
     }
   }
 }
</script>

<style scoped>
 img {
     width: 100px;
 }

 h1 {
     margin-bottom: 0px;
 }
 h2 {
     margin-top: 0px;
     font-size: 1.2em;
     font-weight: normal;
     margin-bottom: 50px;
 }
 .narrow {
     width: 170px;
 }
 .wide {
     width: 370px;
 }
</style>


